.tag-button {
    padding: 5px;
    background-color: #cfeed6;
    border-radius: 10%;
    margin: 5px;
}

.tag-button:hover {
    background-color: #cfeed67f;
}

.card {
    padding: 20px;
    width: 100%;
    background-color: rgb(255, 215, 175);
    margin: 10px 0;
}

.close {
    border: none;
    padding: 10px;
    background-color: #cfeed6;
    margin: 0 10px;
}

.tag-filter {
    position: relative;
    justify-content: center;
    padding: 20px 10px;
}

.tags-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 20px 300px;
}
